'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import Navigation from '@/components/Navigation';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { productsAPI } from '@/lib/api';
import { useCart } from '@/contexts/CartContext';
import { useAuth } from '@/contexts/AuthContext';
import { toast } from 'sonner';

export default function HomePage() {
  const [featuredProducts, setFeaturedProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const { addToCart } = useCart();
  const { isAuthenticated } = useAuth();

  useEffect(() => {
    loadFeaturedProducts();
  }, []);

  const loadFeaturedProducts = async () => {
    try {
      const response = await productsAPI.getAll();
      if (response.success) {
        // Get first 3 products as featured
        setFeaturedProducts(response.data.slice(0, 3));
      }
    } catch (error) {
      console.error('Failed to load featured products:', error);
      toast.error('Failed to load featured products');
    } finally {
      setLoading(false);
    }
  };

  const handleAddToCart = async (productId) => {
    if (!isAuthenticated) {
      toast.error('Please login to add items to cart');
      return;
    }

    const result = await addToCart(productId, 1);
    if (result.success) {
      toast.success('Item added to cart successfully!');
    } else {
      toast.error(result.error || 'Failed to add item to cart');
    }
  };

  return (
    <div className="min-h-screen bg-white">
      <Navigation />
      
      {/* Hero Section */}
      <section className="relative bg-gray-50 py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-center">
            <div className="space-y-8">
              <h1 className="text-5xl lg:text-6xl font-bold text-gray-900 leading-tight" style={{ fontFamily: 'Playfair Display, serif' }}>
                Exquisite
                <br />
                <span className="text-gray-600">Jewellery</span>
                <br />
                Collection
              </h1>
              <p className="text-xl text-gray-600 leading-relaxed">
                Discover our handcrafted jewelry pieces, each designed with precision and elegance. 
                From timeless classics to contemporary designs, find the perfect piece to express your unique style.
              </p>
              <div className="flex flex-col sm:flex-row gap-4">
                <Link href="/products">
                  <Button size="lg" className="bg-gray-900 hover:bg-gray-800 text-white px-8 py-3">
                    Shop Collection
                  </Button>
                </Link>
                <Link href="/products/category/rings">
                  <Button variant="outline" size="lg" className="px-8 py-3">
                    View Rings
                  </Button>
                </Link>
              </div>
            </div>
            <div className="relative">
              <img 
                src="https://placehold.co/600x700?text=Elegant+jewelry+showcase+with+diamond+rings+necklaces+and+earrings+in+luxury+display" 
                alt="Elegant jewelry showcase featuring diamond rings, necklaces, and earrings in a luxury display setting"
                className="w-full h-auto rounded-lg shadow-2xl"
                onError={(e) => {
                  e.target.onerror = null;
                  e.target.src = 'https://placehold.co/600x700?text=Jewelry+Collection';
                }}
              />
            </div>
          </div>
        </div>
      </section>

      {/* Featured Products Section */}
      <section className="py-20 bg-white">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-16">
            <h2 className="text-4xl font-bold text-gray-900 mb-4" style={{ fontFamily: 'Playfair Display, serif' }}>
              Featured Collection
            </h2>
            <p className="text-xl text-gray-600 max-w-2xl mx-auto">
              Handpicked pieces from our exclusive collection, crafted with the finest materials and attention to detail.
            </p>
          </div>

          {loading ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
              {[1, 2, 3].map((i) => (
                <Card key={i} className="overflow-hidden">
                  <div className="animate-pulse">
                    <div className="bg-gray-200 h-80 w-full"></div>
                    <CardContent className="p-6">
                      <div className="h-4 bg-gray-200 rounded mb-2"></div>
                      <div className="h-4 bg-gray-200 rounded w-2/3 mb-4"></div>
                      <div className="h-8 bg-gray-200 rounded"></div>
                    </CardContent>
                  </div>
                </Card>
              ))}
            </div>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
              {featuredProducts.map((product) => (
                <Card key={product.id} className="overflow-hidden hover:shadow-lg transition-shadow duration-300">
                  <div className="relative">
                    <img 
                      src={product.image} 
                      alt={product.name}
                      className="w-full h-80 object-cover"
                      onError={(e) => {
                        e.target.onerror = null;
                        e.target.src = `https://placehold.co/400x400?text=${encodeURIComponent(product.name)}`;
                      }}
                    />
                  </div>
                  <CardContent className="p-6">
                    <h3 className="text-xl font-semibold text-gray-900 mb-2" style={{ fontFamily: 'Playfair Display, serif' }}>
                      {product.name}
                    </h3>
                    <p className="text-gray-600 mb-4 line-clamp-2">
                      {product.description}
                    </p>
                    <div className="flex items-center justify-between">
                      <span className="text-2xl font-bold text-gray-900">
                        ${product.price.toFixed(2)}
                      </span>
                      <div className="flex gap-2">
                        <Link href={`/products/${product.id}`}>
                          <Button variant="outline" size="sm">
                            View Details
                          </Button>
                        </Link>
                        <Button 
                          size="sm" 
                          className="bg-gray-900 hover:bg-gray-800"
                          onClick={() => handleAddToCart(product.id)}
                        >
                          Add to Cart
                        </Button>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          )}

          <div className="text-center mt-12">
            <Link href="/products">
              <Button size="lg" variant="outline" className="px-8 py-3">
                View All Products
              </Button>
            </Link>
          </div>
        </div>
      </section>

      {/* Categories Section */}
      <section className="py-20 bg-gray-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-16">
            <h2 className="text-4xl font-bold text-gray-900 mb-4" style={{ fontFamily: 'Playfair Display, serif' }}>
              Shop by Category
            </h2>
            <p className="text-xl text-gray-600">
              Explore our diverse range of jewelry categories
            </p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
            {[
              { name: 'Rings', href: '/products/category/rings', image: 'Elegant+diamond+and+gold+rings+collection' },
              { name: 'Necklaces', href: '/products/category/necklaces', image: 'Beautiful+pearl+and+gold+necklaces+display' },
              { name: 'Earrings', href: '/products/category/earrings', image: 'Stunning+diamond+and+gemstone+earrings+collection' },
              { name: 'Bracelets', href: '/products/category/bracelets', image: 'Luxurious+gold+and+diamond+bracelets+showcase' }
            ].map((category) => (
              <Link key={category.name} href={category.href}>
                <Card className="overflow-hidden hover:shadow-lg transition-shadow duration-300 cursor-pointer">
                  <div className="relative">
                    <img 
                      src={`https://placehold.co/300x300?text=${category.image}`}
                      alt={`${category.name} collection`}
                      className="w-full h-48 object-cover"
                      onError={(e) => {
                        e.target.onerror = null;
                        e.target.src = `https://placehold.co/300x300?text=${category.name}`;
                      }}
                    />
                    <div className="absolute inset-0 bg-black bg-opacity-40 flex items-center justify-center">
                      <h3 className="text-2xl font-bold text-white" style={{ fontFamily: 'Playfair Display, serif' }}>
                        {category.name}
                      </h3>
                    </div>
                  </div>
                </Card>
              </Link>
            ))}
          </div>
        </div>
      </section>

      {/* Footer */}
      <footer className="bg-gray-900 text-white py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            <div>
              <h3 className="text-2xl font-bold mb-4" style={{ fontFamily: 'Playfair Display, serif' }}>
                Elegant Jewellery
              </h3>
              <p className="text-gray-300">
                Crafting exquisite jewelry pieces with precision, elegance, and timeless beauty.
              </p>
            </div>
            <div>
              <h4 className="text-lg font-semibold mb-4">Quick Links</h4>
              <div className="space-y-2">
                <Link href="/products" className="block text-gray-300 hover:text-white transition-colors">
                  All Products
                </Link>
                <Link href="/products/category/rings" className="block text-gray-300 hover:text-white transition-colors">
                  Rings
                </Link>
                <Link href="/products/category/necklaces" className="block text-gray-300 hover:text-white transition-colors">
                  Necklaces
                </Link>
                <Link href="/products/category/earrings" className="block text-gray-300 hover:text-white transition-colors">
                  Earrings
                </Link>
              </div>
            </div>
            <div>
              <h4 className="text-lg font-semibold mb-4">Contact Info</h4>
              <div className="space-y-2 text-gray-300">
                <p>Email: info@elegantjewellery.com</p>
                <p>Phone: (555) 123-4567</p>
                <p>Address: 123 Jewelry Lane, City, State 12345</p>
              </div>
            </div>
          </div>
          <div className="border-t border-gray-700 mt-8 pt-8 text-center text-gray-300">
            <p>&copy; 2024 Elegant Jewellery. All rights reserved.</p>
          </div>
        </div>
      </footer>
    </div>
  );
}
